.list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 500px;
  padding: 0;
  list-style: none;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  padding: 10px 20px;
  background-color: #ffffff;
  color: #659dbd;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid transparent;
  border-radius: 8px;
  cursor: pointer;
}

.button:hover,
.buton:focus {
  color: #ffffff;
  background-color: #659dbd;
  border: 2px solid #ffffff;
}
